ul[role="menu"] {
	background: -webkit-gradient(linear, left bottom, right top, from(#000000), to(#00008B));
	background: -webkit-linear-gradient(bottom left, #000000, #00008B);
	background: -o-linear-gradient(bottom left, #000000, #00008B);
	background: linear-gradient(to top right, #000000, #00008B);
	border-width: 3px;
	border-style: solid;
	border-color: #00008B;
}



